.App {
  display: flex; }
  .App .main-app {
    display: inline-block;
    order: 1;
    width: 100%; }
    @media (min-width: 768px) {
      .App .main-app {
        max-width: 450px;
        width: 40%; } }
  .App .slideshow-message {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .App .slideshow-message h1, .App .slideshow-message p {
      background-color: rgba(0, 0, 0, 0.6);
      color: white;
      padding: 10px;
      margin: 0;
      text-align: center; }
    .App .slideshow-message h1 {
      letter-spacing: .05rem; }
    .App .slideshow-message p {
      margin: 10px 0; }
