.slideup-panel {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  .slideup-panel .menu-button {
    width: 100%;
    box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.25);
    background-color: white;
    padding: 15px 5px;
    text-align: center;
    margin-top: auto;
    cursor: pointer;
    border-bottom: 1px solid rgba(87, 169, 164, 0.57);
    font-weight: bold;
    font-size: 1.05rem;
    letter-spacing: .01rem;
    color: #1c9995; }
  .slideup-panel .main {
    overflow: hidden;
    transition: max-height 350ms, min-height 350ms, padding 350ms;
    transition-timing-function: ease-in;
    background-color: white;
    padding: 15px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column; }
    .slideup-panel .main .terms {
      text-align: justify;
      padding: 0 5px; }
    .slideup-panel .main form {
      padding: 0 10px;
      margin-bottom: 10px; }
      @media (min-width: 768px) {
        .slideup-panel .main form {
          margin-top: 30px; } }
    .slideup-panel .main footer {
      text-align: center;
      margin-top: auto;
      margin-bottom: 10px; }
      .slideup-panel .main footer a {
        color: #17a2b8;
        font-size: .95rem; }
    @media (min-width: 768px) {
      .slideup-panel .main {
        flex: 1;
        padding: 25px;
        box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.25); } }
    .slideup-panel .main .title, .slideup-panel .main .message {
      text-align: center; }
    .slideup-panel .main .title {
      margin: 30px 0 20px;
      letter-spacing: .05rem; }
    .slideup-panel .main .message {
      margin-bottom: 40px; }
    .slideup-panel .main input:not([type="submit"]) {
      width: 100%;
      margin-bottom: 12px;
      margin-top: 5px;
      font-size: 16px;
      padding: 5px 2px; }
    .slideup-panel .main .button-wrapper {
      position: relative; }
      .slideup-panel .main .button-wrapper .loader {
        position: absolute;
        background-color: #17a2b8;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        width: 90px;
        border-radius: 5px;
        margin-left: calc(50% - 45px);
        cursor: progress;
        display: flex;
        justify-content: center;
        align-items: center; }
        .slideup-panel .main .button-wrapper .loader::after {
          content: "";
          display: block;
          height: 22px;
          width: 22px;
          border-radius: 100%;
          border-left: 3px solid #fb5563;
          border-right: 3px solid #fb5563;
          border-top: 3px solid rgba(255, 255, 255, 0.3);
          border-bottom: 3px solid rgba(255, 255, 255, 0.3);
          animation: spin 1s linear infinite; }
        .slideup-panel .main .button-wrapper .loader.hide {
          display: none; }
        .slideup-panel .main .button-wrapper .loader.show {
          display: flex; }
  .slideup-panel.hidden .menu-button {
    color: #17a2b8; }
  .slideup-panel.hidden .main {
    max-height: 0px;
    min-height: 0;
    padding: 0px; }
    @media (min-width: 768px) {
      .slideup-panel.hidden .main {
        min-height: 0;
        max-height: none;
        padding: 25px; } }
  .slideup-panel.show .main {
    min-height: calc(100vh - 44px);
    max-height: calc(100vh - 44px);
    overflow: scroll;
    -webkit-overflow-scrolling: touch; }
    @media (min-width: 768px) {
      .slideup-panel.show .main {
        min-height: 0;
        max-height: none; } }

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@-moz-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@-o-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
