.text-input {
  text-align: left;
  font-size: .95rem;
  position: relative; }
  .text-input::after {
    content: "";
    display: block;
    background-color: #17a2b8;
    width: 0;
    position: absolute;
    height: 1px;
    bottom: 0px;
    margin: 0 50%;
    transition: width 250ms, margin 250ms;
    transition-timing-function: ease-in; }
  .text-input input {
    box-sizing: border-box;
    border-radius: 0;
    width: 100%;
    margin-top: 7px;
    padding: 0px 3px 5px;
    min-height: 30px;
    font-size: 16px;
    display: block;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: rgba(0, 0, 0, 0);
    border: 0px;
    border-bottom: 1px solid #808c9c; }
    .text-input input:disabled {
      color: #808c9c;
      cursor: not-allowed; }
    @media (min-width: 768px) {
      .text-input input {
        font-size: .9rem; } }
    .text-input input::placeholder {
      color: #808c9c; }
  .text-input.inverse input {
    color: #212d40; }
  .text-input.inverse::after {
    background-color: #212d40; }
  .text-input.focus::after {
    width: 100%;
    margin: 0%; }
  .text-input.error input::placeholder {
    color: #fb5563; }
  .text-input.error::after {
    width: 100%;
    margin: 0%;
    background-color: #fb5563; }
